import React, {Component} from 'react'
import axios from 'axios';
import {withAuth} from './../lib/Auth'

class EditStudent extends Component {
  state = {
    isLoading: true
  };

  componentDidMount(){
    this.getTheStudentInfo();
  }
  
  getTheStudentInfo = () => {
    axios.get(`${process.env.REACT_APP_API_URL}/settings/editstudent`, { withCredentials: true})
    .then( (response) => {
      const studentInfo = response.data;
      console.log('studentInfo', studentInfo)
      this.setState({studentInfo, isLoading: false});

      console.log('this.state', this.state)
      })
      .catch( (err) => console.log(err));
  }

  handleChange = e => {  
    let {name, value, type} = e.target;
    if (type === 'radio') {
      let selected = document.querySelector('input[name="cohort"]:checked').value;
      value = selected;
    }
    this.setState({[name]: value});
  }

  handleSubmit = e => {
    e.preventDefault();
    const { studentName, cohort } = this.state;
    // const { id } = this.props.user._id; 
                                                
    axios.put(`${process.env.REACT_APP_API_URL}/settings/editstudent`, {
      studentName: studentName, 
      cohort: cohort
    }, { 
      withCredentials: true
    })
    .then( () => {
      
    })
    .catch( error => console.log(error) )
  }

  render() {
    const { isLoading } = this.state;
    console.log(this.state);
    
    return (
       !isLoading ?
      
      (<div>
        <form>
        <label>Student Name:</label>
            <input
              type="text"
              name="studentName"
              placeholder={this.state.studentInfo.studentName}
              value={this.state.studentInfo.studentName}
              onChange={this.handleChange}
            />

            <label>Choose your cohort:</label>
            <input
              type="radio"
              name="cohort"
              value="ux/ui"
              checked={this.state.studentInfo.cohort === "ux/ui"}
              onChange={this.handleChange} /> UX/UI 
            <input
              type="radio"
              name="cohort"
              value="web"
              checked={this.state.studentInfo.cohort === "web"}
              onChange={this.handleChange} /> Web Dev 
            <input
              type="radio"
              name="cohort"
              value="data"
              checked={this.state.studentInfo.cohort === "data"}
              onChange={this.handleChange} /> Data 

            <button onClick={this.handleSubmit}>Done</button>
            <button onClick={this.props.history.goBack}> Go Back</button>
          </form>
      </div>)
      : null
        
    )
  }
}

export default withAuth(EditStudent);